.round-btn {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  border: none;

  &:hover {
    box-shadow: 0 0 12px -5px rgba(0, 0, 0, 0.5);
  }
  img {
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
  }
}
